raul-vertical-menu .r-vertical-menu__mobile__modal {
  background-color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  transition: all .2s ease-in-out;
  pointer-events: none;
  z-index: 10000;
  transform: scale(0.9);
}

raul-vertical-menu .r-vertical-menu__mobile__modal__visible {
  display: block;
  opacity: 1;
  pointer-events: auto;
  transform: scale(1);
}

raul-vertical-menu .r-vertical-menu__mobile__modal .r-vertical-menu__container {
