  border-width: 1px;
  border-style: solid;
  border-color: #EBEDEE;
  border-radius: 0.25rem;
  position: relative;
  overflow: hidden;
  transition: height ease-in-out 0.25s;
}

raul-vertical-menu .r-vertical-menu__container ul li {
  border-bottom-width: 1px;
  border-color: #EBEDEE;
}

raul-vertical-menu .r-vertical-menu__container ul li:last-child {
  border-color: transparent;
}

raul-vertical-menu .r-vertical-menu__container ul .r-vertical-menu--active {
  background-color: #F7F8F9;
}

raul-vertical-menu .r-vertical-menu__container ul.r-vertical-menu--no-animation {
  opacity: 1;
}

raul-vertical-menu .r-vertical-menu__container ul.r-vertical-menu__container--previous, raul-vertical-menu .r-vertical-menu__container ul.r-vertical-menu__container--next {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
  -webkit-animation-duration: 0.25s;
  animation-duration: 0.25s;
}

raul-vertical-menu .r-vertical-menu__container ul.r-vertical-menu__container--previous {
  -webkit-animation-name: fade-out-animation-left;
  animation-name: fade-out-animation-left;
}

@-webkit-keyframes fade-out-animation-left {
  0% {
    opacity: 1;
    pointer-events: none;
    transform: translateX(0px);
    touch-action: none;
  }

  100% {
    opacity: 0;
    transform: translateX(-280px);
  }
}

@keyframes fade-out-animation-left {
  0% {
    opacity: 1;
    pointer-events: none;
    transform: translateX(0px);
    touch-action: none;
  }

  100% {
    opacity: 0;
    transform: translateX(-280px);
  }
}

raul-vertical-menu .r-vertical-menu__container ul.r-vertical-menu__container--next {
  -webkit-animation-name: fade-out-animation-right;
  animation-name: fade-out-animation-right;
}

@-webkit-keyframes fade-out-animation-right {
  0% {
    opacity: 1;
    pointer-events: none;
    transform: translateX(0px);
    touch-action: none;
  }

  100% {
    opacity: 0;
    pointer-events: auto;
    transform: translateX(280px);
  }
}

@keyframes fade-out-animation-right {
  0% {
    opacity: 1;
    pointer-events: none;
    transform: translateX(0px);
    touch-action: none;
  }

  100% {
    opacity: 0;
    pointer-events: auto;
    transform: translateX(280px);
  }
}

raul-vertical-menu .r-vertical-menu__container ul.r-vertical-menu--fade-left, raul-vertical-menu .r-vertical-menu__container ul.r-vertical-menu--fade-right {
  opacity: 1;
  -webkit-animation-timing-function: ease-in-out;
  animation-timing-function: ease-in-out;
  -webkit-animation-duration: 0.25s;
  animation-duration: 0.25s;
}

raul-vertical-menu .r-vertical-menu__container ul.r-vertical-menu--fade-left {
  -webkit-animation-name: fade-in-animation-left;
  animation-name: fade-in-animation-left;
}

@-webkit-keyframes fade-in-animation-left {
  0% {
    opacity: 0;
    pointer-events: none;
    transform: translateX(-280px);
    touch-action: none;
  }

  100% {
    opacity: 1;
    pointer-events: auto;
    transform: translateX(0px);
