  animation-name: fade-in-animation-right;
}

@-webkit-keyframes fade-in-animation-right {
  0% {
    opacity: 0;
    pointer-events: none;
    transform: translateX(280px);
    touch-action: none;
  }

  100% {
    opacity: 1;
